/* review.tsx */

.review {
  width: 100%;
  position: relative;
  align-items: center;
  margin-bottom: 2rem;
  margin-top: 0.5rem;
  display: inline-block;
  animation: appear 1s 1 ease-in-out;
}

@keyframes appear {
  from {opacity: 0;}
  to {opacity: 1;}
}

.review_info {
  margin-top: 1.5rem;
}

.review_author {
  color: white;
  margin-bottom: 5px;
}

.author {
  font-weight: 700;
  font-size: 18px;
  text-align: center;
  color: #ffffff;

  text-shadow: 0 -1px 4px #fff, 0 -2px 10px #ff0, 0 -10px 20px #ff8000,
    0 -18px 40px #f00;
}

.created_date {
  text-align: center;
}

.review_content_con {
  margin-left: 5%;
  margin-right: 5%;
  padding: 10px;
  text-align: center;
  background: rgb(255, 255, 255);
  -webkit-box-shadow: inset -1px 3px 8px 5px #ff1f6a, 2px 5px 16px 0px #9d0d12,
    5px 5px 15px 5px rgba(0, 0, 0, 0);
  box-shadow: inset -1px 3px 8px 5px #310416, 2px 5px 16px 0px #610529,
    5px 5px 15px 5px rgba(0, 0, 0, 0);
}

