.not_path{
    width: 100%;
    height: 100vh;
    position: relative;
}

.not_path_inner_con{
    position: fixed;
    width: 100%;
    text-align: center;
    transform: translate(-50%,-50%);
    left: 50%;
    top: 50%;
    color:rgb(255, 255, 255);
    text-shadow: 2px 10px 5px black;
}

.not_path_backButton{
    margin-top: 2rem;
    padding: 5px;
    font-size: 16px;
    cursor: pointer;
}