/* DetailTaps */
.detail_taps {
  padding-top: 5rem;
  text-align: center;
  width: 100%;
  animation: appear 1s 1 ease-in-out;
}

@keyframes appear {
  from {opacity: 0;}
  to {opacity: 1;}
}

/* 탭 버튼 */
.tap_btn {
  font-size: 16px;
  height: 40px;
  font-weight: bolder;
  padding: 10px 25px 10px 25px;
  color: rgb(235, 196, 196);
  width: 200px;
  padding: auto;
  margin: 5px;
  border: none;
  border-radius: 1px;
  -webkit-box-shadow: 0px 10px 13px -7px #000000,
    5px 5px 15px 5px rgba(0, 0, 0, 0);
  box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0, 0, 0, 0);
}
.tap_btn:hover {
  color: #231f1f;
  box-shadow: 200px 200px 15px goldenrod inset, #fff 0 -1px 4px, #ff0 0 -2px 10px, #ff8000 0 -10px 20px,
    red 0 -18px 40px, 5px 5px 15px 5px rgba(0, 0, 0, 0);
  cursor: pointer;
}
